function Modal(props) {

  function cancleHandler() {
    props.onCancle()
  }
  function confirmHandler() {
    props.onConfirm()
  }
    return (
      <div className="modal">
        <p>Are you sure?</p>
        <button onClick={cancleHandler} className="btn btn--alt">
          Cancle
        </button>
        <button onClick={confirmHandler} className="btn">
          Confirm
        </button>
      </div>
    );
}
export default Modal;